import React from 'react';
import { Card, Row, Col, Icon, Button } from 'antd';
const ButtonGroup = Button.Group;

class ButtonDemo extends React.Component {

  render() {

    return (
      <div>
        <Row gutter={32}>
          <Col span={12}>
            <Card noHovering title="Antd ">
              <Button type="primary"></Button>{' '}
              <Button></Button>{' '}
              <Button type="dashed"></Button>{' '}
              <Button type="danger"></Button>
            </Card>
          </Col>
          <Col span={12}>
            <Card noHovering title="Antd ">
              <div>
                <Button type="primary" shape="circle" icon="search" />{' '}
                <Button type="primary" icon="search"></Button>{' '}
                <Button shape="circle" icon="search" />{' '}
                <Button icon="search"></Button>{' '}
                <Button shape="circle" icon="search" />{' '}
                <Button icon="search"></Button>{' '}
                <Button type="dashed" shape="circle" icon="search" />{' '}
                <Button type="dashed" icon="search"></Button>{' '}
                <Button type="default" shape="circle" icon="forward" />{' '}
                <Button type="default" shape="circle" icon="caret-right" />{' '}
              </div>
            </Card>
          </Col>
        </Row>
        <Row gutter={32} style={{ marginTop: '20px' }}>
          <Col span={12}>
            <Card noHovering title="Antd ">
              <h2>Large</h2>
              <Button type="primary" shape="circle" icon="download" size="large" />{' '}
              <Button type="primary" icon="download" size="large"></Button>{' '}
              <Button type="primary" size="large"></Button>{' '}
              <Button.Group size="large">
                <Button type="primary">
                  <Icon type="left" />
                </Button>
                <Button type="primary">
                  <Icon type="right" />
                </Button>
              </Button.Group>
              <h2>Default</h2>
              <Button type="primary" shape="circle" icon="download" size="default" />{' '}
              <Button type="primary" icon="download" size="default"></Button>{' '}
              <Button type="primary" size="default"></Button>{' '}
              <Button.Group size="default">
                <Button type="primary">
                  <Icon type="left" />
                </Button>
                <Button type="primary">
                  <Icon type="right" />
                </Button>
              </Button.Group>
              <h2>Small</h2>
              <Button type="primary" shape="circle" icon="download" size="small" />{' '}
              <Button type="primary" icon="download" size="small"></Button>{' '}
              <Button type="primary" size="small"></Button>{' '}
              <Button.Group size="small">
                <Button type="primary">
                  <Icon type="left" />
                </Button>
                <Button type="primary">
                  <Icon type="right" />
                </Button>
              </Button.Group>
            </Card>
          </Col>
          <Col span={12}>
            <Card noHovering title="Antd ">
              <Button type="primary"></Button>{' '}
              <Button type="primary" disabled>()</Button>{' '}
              <Button type="primary" loading> </Button>{' '}
              <Button type="primary" size="small" loading></Button>{' '}
              <Button shape="circle" loading />{' '}
              <Button type="primary" shape="circle" loading />
            </Card>
          </Col>
        </Row>
        <Row gutter={8} style={{ marginTop: '20px' }}>
          <Col span={12}>
            <Card noHovering title="Antd ">
              <h2>Basic</h2>
              <ButtonGroup>
                <Button>Cancel</Button>
                <Button type="primary">OK</Button>
              </ButtonGroup>{' '}
              <ButtonGroup>
                <Button disabled>L</Button>
                <Button disabled>M</Button>
                <Button disabled>R</Button>
              </ButtonGroup>{' '}
              <ButtonGroup>
                <Button type="primary">L</Button>
                <Button>M</Button>
                <Button>M</Button>
                <Button type="dashed">R</Button>
              </ButtonGroup>{' '}

              <h2>With Icon</h2>
              <ButtonGroup>
                <Button type="primary">
                  <Icon type="left" />Go back
                </Button>
                <Button type="primary">
                  Go forward<Icon type="right" />
                </Button>
              </ButtonGroup>{' '}
              <ButtonGroup>
                <Button type="primary" icon="cloud" />
                <Button type="primary" icon="cloud-download" />
              </ButtonGroup>
            </Card>
          </Col>
          <Col span={12}>
            <Card noHovering title="Antd ">
              
              <div style={{ background: 'rgb(190, 200, 200)', padding: '26px 16px 16px' }}>
                <Button type="primary" ghost></Button>{' '}
                <Button ghost></Button>{' '}
                <Button type="dashed" ghost></Button>{' '}
                <Button type="danger" ghost></Button>
              </div>
            </Card>
          </Col>
        </Row>
      </div>
    )
  }
}


export default ButtonDemo;



